import React from "react";
import { InfoCircleOutlined } from "@ant-design/icons";
import { Button, Checkbox, Form, Input, Switch } from "antd";
// 
export default function MenuForm(props) {
  const onFinish = (values) => {
    console.log("Success:", values);
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  return (
    <div>
      {" "}
      <Form
        name="basic"
        labelCol={{
          span: 4,
        }}
        wrapperCol={{
          span: 16,
        }}
        initialValues={{
          remember: true,
        }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        labelWrap={true}
      >
        <Form.Item label="" name="username">
          <Button></Button>
          <Button></Button>
          <Button>/</Button>
        </Form.Item>

        <Form.Item label="" name="password">
          <Input />
        </Form.Item>
        <Form.Item label="" name="password">
          <Input />
        </Form.Item>
        <Form.Item label="" name="password">
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="password"
          tooltip={{
            title: "Tooltip with customize icon",
            icon: <InfoCircleOutlined />,
          }}
        >
          <Input />
        </Form.Item>
        <Form.Item label="" name="password">
          <Input />
        </Form.Item>
        <Form.Item label="" name="password">
          <Input />
        </Form.Item>

        <Form.Item label="" valuePropName="checked">
          <Switch />
        </Form.Item>
        <Form.Item
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}
