const defulatState = {
  userInfo: "",
};

const reducer = (state = defulatState, action) => {
  //  state
  console.log("", action);
  //   state
  const newState = {
    ...state,
  };
  switch (action.type) {
    case "CHANGE_USERINFO":
      return changeUserinfoFn(newState, action);
    default:
      return state;
  }
};
const changeUserinfoFn = (newState, action) => {
  newState.userInfo = action.value;
  return {
    ...newState,
  };
};
export default reducer;
